.Brand_Logo {
  color: black;
  display: flex;
  align-items: center;
  height: auto;
  width: auto;
  .logoSvg {
    height: 40px;
    width: 40px;
  }
  .logoText {
    height: 19px;
    width: 111px;
    margin-left: -10px;
  }
}

@media (min-width: 1600px) {
  .Brand_Logo {
    .logoSvg {
      height: 60px;
      width: 60px;
    }
    .logoText {
      height: 30px;
      width: 150px;
      margin-left: -7px;
    }
  }
}
