/** @format */

.Type0_Account {
  margin: 25px -30px 0 -58px;
}

.Type0_Note {
  /* background-color: #f6f9fb; */
  font-family: Roboto;
  padding: 0px 25px 70px 25px;
}

.Type0_Note img {
  display: block;
  margin: auto;
}

.Type0_Note p {
  width: 70%;
  margin: 20px auto;
  opacity: 0.5;
  font-size: 16px;
  text-align: center;
  color: #24272c;
}

.Type0_Link {
  position: relative;
  margin: -15% auto auto auto;
  width: 85%;
  padding: 35px 25px 35px 25px;
}

.Type0_Link p {
  font-family: Roboto;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.25;
  text-align: center;
  color: #000000;
}

.Type0_Link img {
  width: 220px;
  display: block;
  margin: 30px auto;
}

.Type0_Link .Type0_Button {
  display: flex;
  justify-content: center;
  margin: auto;
  margin-top:10px;
  cursor: pointer;
}

.Done_Linking {
  margin-top: 30px;
  font-family: Roboto;
  text-align: center;
  color: #000;
}

.Done_Linking span {
  font-weight: normal;
  text-decoration: underline;
  color: #12b0ee;
}

p.instruction {
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 15px;
  text-align: right;
  color: #24272c;
  mix-blend-mode: normal;
  opacity: 0.7;
  text-align: center;
  margin-top: 10px;
}

p.skipBtn {
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 15px;
  text-align: right;
  text-decoration-line: underline;
  color: #24272c;
  mix-blend-mode: normal;
  opacity: 0.7;
  text-align: center;
  margin-top: 30px;
  cursor: pointer;
}

p.skipBtn img {
  vertical-align: middle;
  width: 2%;
  margin-left: 5px;
  display: initial;
  margin-top: 0;
  margin-bottom: 0;
}
